<template>
  <div class="news">
    <h1>最新消息</h1>
    <div class="news__container">
      <ul class="news__list">
        <li class="news__list__item"
        v-for="(item, index) in news"
        :key="index">
          <a href="#" class="news__a"
          @click='go_page(`/front/news/${item.id}`)'>
            <div class="news__img">
              <img :src='item.banner' alt="">
            </div>
            <div class="news__main">
              <div class="news__main__header">
                <div class="news__title">{{item.title}}</div>
                <div class="news__date">{{item.date}}</div>
              </div>
              <div class="news__description" v-html="item.description"></div>
              <div class="news__link">READ MORE</div>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    news() {
      let news = [];
      this.$bus.$on('son_get_data', (data) => {
        news = data.news;
      });
      this.$bus.$emit('son_call_data');
      return news;
    },
  },
  methods: {
    go_page(route) {
      this.$router.push(route);
    },
  },
};
</script>
